.product-accordion {
    padding: 12px 0;

    .subtitle1 {
        margin-bottom: 8px;
    }

    &__text {
        margin-bottom: 12px;
    }

    ul {
        margin-bottom: 12px;
    }

    li {
        list-style-type: disc;
        margin-left: 24px;
    }
}


.chevron-down {
    margin-left: auto;
    transition: transform 0.25s cubic-bezier(0, 0, 0, 1);
}

.szh-accordion {
    &__item {
        border-bottom: 1px solid #f5f5f5;

        &-btn {
            font-size: 17px;
            font-weight: 500;
            line-height: 24px;
            letter-spacing: -0.4px;
            padding: 12px 0;
            cursor: pointer;
            display: flex;
            align-items: center;
            width: 100%;
            text-align: left;
        }

        &-content {
            transition: height 0.25s cubic-bezier(0, 0, 0, 1);
        }

        &--expanded {
            .szh-accordion__item-btn {}

            .chevron-down {
                transform: rotate(180deg);
            }
        }
    }
}