.header {
    position: fixed;
    z-index: 50;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 100%;
    max-width: 560px;
    background: var(--white);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__left {
        flex-shrink: 0;
        margin-right: 8px;
        display: flex;
        align-items: center;
    }

    &__right {
        flex-shrink: 0;
        margin-left: 8px;
    }

    &__logo {
        width: 157px;
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    &__icon {
        width: 52px;
        height: 52px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    &__title {
        margin-left: 6px;
        font-size: 17px;
        font-weight: 600;
        line-height: 20px;
        color: #010101;
    }
}

.mobMenu {
    position: fixed;
    z-index: 49;
    left: 50%;
    transform: translate(-50%, 0);
    width: 100%;
    max-width: 560px;
    height: 0;
    background: var(--white);
    overflow: auto;
    -ms-overflow-style: none;
    overflow-y: scroll;
    scrollbar-width: none;

    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }

    &__inner {
        height: 100%;
        display: flex;
        flex-direction: column;
        padding-top: 12px;
    }

    &__fixedDown {
        margin: auto;
        margin-bottom: 0;
        width: 100%;
    }

    &__under {
        background: var(--minor);
        padding: 24px 16px;
        padding-bottom: 12px;
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.mobileMenu {
    padding: 0 16px;
    padding-bottom: 12px;
}

.mobileMenuItem {
    min-height: 48px;
    display: flex;
    align-items: center;

    &__text {
        width: 100%;
        font: 400 20px/24px var(--font);
        letter-spacing: -0.4px;
        font-weight: 600;
    }

    &__icon {
        flex-shrink: 0;
        width: 24px;
        display: flex;
        align-items: center;
        margin-left: 8px;
    }

    &_unavailable {
        pointer-events: none;
    }

    &_unavailable .mobileMenuItem__text {
        color: var(--gray1);
    }
}

.mobileSectionsMenu {
    border-top: 1px solid var(--gray2);
    padding: 0 16px;
    padding-top: 12px;
    margin-bottom: 12px;
}

.mobMenuUnavailable {
    padding: 0 16px;
    padding-top: 12px;

    &__title {
        margin-bottom: 24px;
        text-align: center;
    }
}

.mobileCommonMenu {
    padding: 0 16px;
    margin-bottom: 12px;
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(2, 1fr);


    &__item {
        display: block;
        padding: 4px 0;
        color: var(--gray);
    }

    &__item:hover {
        color: var(--dark);
    }
}


.mobMenuHelp {
    margin-bottom: 10px;

    &__title {
        opacity: 0.5;
        margin-bottom: 5px;
    }
}


.mobMenuPayment {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    margin-left: 8px;

    &__item {
        display: flex;
        align-items: center;
        margin-right: 5px;
    }

    &__item:last-child {
        margin-right: 0;
    }
}