.mainBanner {
    min-height: 340px;
    background: url("../../assets/images/main-banner-bg.webp") no-repeat;
    background-size: cover;
    background-position: bottom center;
    padding: 36px 0;
    margin-bottom: 24px;

    &__content {
        max-width: 240px;
    }

    &__title {
        margin-bottom: 12px;
    }

    &__subtitle {
        margin-bottom: 24px;
    }
}

.footnote {
    font: 400 13px/16px var(--font);
    letter-spacing: -0.08px;
}

.footnote strong {
    font-weight: 500;
}

.caption2 {
    font: 400 11px/12px var(--font);
    letter-spacing: 0.04px;
}

.caption2 strong {
    font-weight: 600;
}

.textBlock p {
    margin-bottom: 8px;
}

.textBlock p:last-child {
    margin-bottom: 0;
}

.textBlock a {
    color: var(--base);
}

.textBlock a:hover {
    text-decoration: underline;
}

.textBlock ul {
    margin-bottom: 8px;
}

.textBlock ul:last-child {
    margin-bottom: 0;
}

.textBlock ul li {
    display: block;
    position: relative;
    padding-left: 24px;
    margin-bottom: 2px;
}

.textBlock ul li:last-child {
    margin-bottom: 0;
}

.textBlock ul li::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 8px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: var(--base);
}

.textBlock ol {
    list-style-type: decimal;
    margin-left: 24px;
    margin-bottom: 8px;
}

.textBlock ol:last-child {
    margin-bottom: 0;
}

.textBlock ol li {
    margin-bottom: 2px;
}

.textBlock ol li:last-child {
    margin-bottom: 0;
}

.section-wrapper {
    padding: 0 0 30px;
}

.section {
    padding: 30px 0;
}

.section.section_noPad {
    padding: 0;
}

.section.section_noPadT {
    padding-top: 0;
}

.sectionHead {
    margin-bottom: 20px;
}

.sectionHead:last-child {
    margin-bottom: 0;
}

.sectionHead__title {
    margin-bottom: 12px;
}

.sectionHead__title:last-child {
    margin-bottom: 0;
}

.pageHead {
    margin-bottom: 16px;
}

.pageHead_smMg {
    margin-bottom: 12px;
}

.mainAdvanWrap {
    display: grid;
    grid-column-gap: 12px;
    grid-row-gap: 20px;
    grid-template-columns: repeat(2, 1fr);
}

.mainAdvan {
    display: flex;
    align-items: center;
}

.mainAdvan__icon {
    flex-shrink: 0;
    width: 32px;
    margin-right: 10px;
    display: flex;
    align-items: center;
}

.mainAdvan__text {
    width: 100%;
}

.mainAccess {
    min-height: 340px;
    background: url("../../assets/images/main-access-bg.webp") no-repeat;
    background-size: cover;
    background-position: bottom center;
    padding: 40px 0;
    margin: 30px 0;
}

.mainAccess__image {
    display: none;
    position: absolute;
    z-index: 1;
}

.mainAccess .wrapper {
    position: relative;
    z-index: 2;
}

.mainAccess__title {
    margin-bottom: 24px;
}

.mainAccessItem {
    max-width: 207px;
    display: flex;
    align-items: flex-start;
    margin-bottom: 16px;
    position: relative;
}

.mainAccessItem::before {
    content: "";
    position: absolute;
    top: 34px;
    left: 13px;
    width: 1px;
    height: calc(100% - 28px);
    background: var(--base);
}

.mainAccessItem__num {
    flex-shrink: 0;
    width: 24px;
    height: 24px;
    background: var(--base);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font: 400 16px/1 var(--font);
    color: var(--white);
    margin-right: 16px;
}

.mainAccessItem__content {
    max-width: 100%;
    padding-top: 2px;
}

.mainAccessItem__title {
    margin-bottom: 6px;
}

.mainAccessItem__title a {
    border-bottom: 1px solid var(--base);
    color: var(--base);
}

.mainAccessItem__title a:hover {
    border-bottom-color: var(--dark);
    color: var(--dark);
}

.mainAccessItem:last-child {
    margin-bottom: 0;
}

.mainAccessItem:last-child::before {
    display: none;
}

.faqItem {
    border-bottom: 1px solid var(--gray1);


    &__head {
        padding: 16px 0;
        display: flex;
        align-items: flex-start;
    }

    &__title {
        width: 100%;
    }

    &__arrow {
        flex-shrink: 0;
        width: 24px;
        display: flex;
        align-items: center;
        margin-left: 8px;
    }

    &__text {
        transition: 400ms;
        height: 0;
        visibility: hidden;
        opacity: 0;
        margin-top: 0;
        padding-bottom: 0;

        &.active {
            opacity: 1;
            visibility: visible;
            height: auto;
            margin-top: -8px;
            padding-bottom: 16px;
        }
    }

    .faqItem:first-child .faqItem__head {
        padding-top: 0;
    }

    &.active .faqItem__arrow {
        transform: scale(1, -1);
    }
}

@media screen and (max-width: 369px) {
    .mainAdvan__icon {
        width: 28px;
        margin-right: 8px;
    }
}