.sorting {

    &__react-modal-sheet-container {
        border-top-left-radius: 16px !important;
        border-top-right-radius: 16px !important;
        padding: 0 16px 16px;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0 14px;
    }

    &__close {}

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 14px 0;
        border-bottom: 1px solid #f5f5f5;
        cursor: pointer;


        &:last-child {
            border: 0
        }

        &__button {
            border: 1px solid var(--gray1);
            width: 20px;
            height: 20px;
            border-radius: 50%;
            transition: 200ms;

            &-active {
                border: 3px solid var(--dark);
                position: relative;

                &:before {
                    content: '';
                    display: block;
                    width: 12px;
                    height: 12px;
                    border-radius: 50%;
                    background-color: var(--dark);
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    margin: auto;
                }
            }
        }
    }
}