.catalog-id {
    padding: 0 0 8px;

    &__products {
        display: flex;
        flex-wrap: wrap;
    }

    &__product {
        flex: 1 0 calc(50% - 12px);
        width: 50%;
        margin-bottom: 32px;

        &:nth-child(2n) {
            margin-left: 12px;
        }
    }

    &__filter {
        margin: 12px 0;
        padding: 12px 0;
        display: flex;
        justify-content: space-between;

        &__button {
            display: flex;
            align-items: center;

            &__icon {
                margin: 0 6px;

                &_arrow {
                    transform: rotate(90deg);

                    &-open {

                        transform: rotate(270deg);
                    }
                }

                &_filter {
                    margin-right: 10px;
                }
            }
        }
    }

    &__result {
        margin-bottom: 16px;
        color: var(--gray1);
    }
}