.swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    background: var(--gray2);
    border-radius: 50%;
}

.swiper-pagination-bullet-active {
    background: var(--dark);
}

.mainSlider {
    &__dots {
        margin-top: 16px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
    }
}

.mainSliderItem {
    flex-shrink: 0;

    &__image {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 16px;
    }

    &__image img {
        width: 100%;
        max-width: 420px;
    }

    &__title {
        text-align: center;
    }
}