.tabs {
    height: 32px;
    width: 100%;
    display: flex;
    align-items: center;
    overflow-x: auto;
    overflow-y: hidden;
    padding: 0 16px;
    border-bottom: 1px solid #e9e9e9;

    &__item {
        margin-right: 14px;
        cursor: pointer;

        p {
            color: var(--gray);
        }

        &_active {
            position: relative;

            p {
                color: var(--dark)
            }

            &:before {
                content: '';
                position: absolute;
                bottom: 0;
                height: 2px;
                background-color: var(--dark);
                left: 0;
                right: 0;
                bottom: -8px;
                z-index: 10;
                border-radius: 2px;
            }
        }
    }
}