.list {
    &__item {
        padding: 16px 0;
        border-bottom: 1px solid #f5f5f5;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;

    }

    li {
        &:last-child {
            .list__item {
                border: 0;
            }
        }
    }

}