.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 24px 0;

    .login-cards {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin-bottom: 12px;

        &__item {
            height: 160px;
            flex: 1;
            background: var(--gray2) center 42px no-repeat;
            border-radius: 12px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            padding-bottom: 42px;

            &_user {
                background-image: url('../../../assets/icons/user-square.svg');
            }

            &_hospital {
                background-image: url('../../../assets/icons/hospital.svg');
            }

            &_shop {
                background-image: url('../../../assets/icons/shop.svg');
            }

        }

        &:nth-child(3) {
            .login-cards__item {
                &:nth-child(1) {
                    margin-right: 8px;
                }
            }
        }
    }
}