.card {
    display: flex;
    flex-direction: column;

    &__image {
        margin-bottom: 8px;
    }

    &__brand {
        text-decoration: underline;
        margin-bottom: 8px;
        font-weight: 500;
    }

    &__title {
        margin-bottom: 8px;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        height: 32px;

    }

    &__price {
        margin-bottom: 8px;
    }
}