.footer {
  margin: auto;
  margin-bottom: 0;
  width: 100%;
}

.footer__inner {
  background: var(--minor);
  padding-top: 24px;
  padding-bottom: 12px;
  margin-top: 30px;
}

.footer__menus {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr;
  padding-bottom: 15px;
}

.footer__contacts {
  border-top: 1px solid var(--gray2);
  padding-top: 15px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 1fr;
}

.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__copyright {
  width: 100%;
  opacity: 0.5;
}

.footerMenu {
  display: grid;
  grid-gap: 12px;
  grid-template-columns: repeat(2, 1fr);
}

.footerMenu__title {
  grid-column: span 2;
  font: 400 15px/20px var(--font);
  letter-spacing: -0.2px;
  font-weight: 600;
}

.footerContact__title {
  opacity: 0.5;
  margin-bottom: 5px;
}

.footerPayment {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  margin-left: 8px;
}

.footerPayment__item {
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.footerPayment__item:last-child {
  margin-right: 0;
}