.pagination {
    display: flex;
    justify-content: space-between;

    &__arrow {
        width: 40px;
        height: 40px;
        flex: auto 0 40px;
        border-radius: 50%;
        border: 1px solid var(--gray1);
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 2px;
    }

    &__arrow_left {
        margin-left: 0;

        svg {
            transform: rotate(180deg);
        }
    }

    &__arrow_right {
        margin-right: 0;

    }
}