.sign-in {
    display: flex;
    flex-direction: column;
    padding-top: 32px;

    &__logo {
        max-width: 157px;
        align-self: center;
        margin-bottom: 40px;
    }

    &__input {
        margin-bottom: 16px;
    }

    &__forgot-password {
        margin: 12px 0 32px;
        align-self: flex-end;
        color: var(--gray);
    }
}